import { Share } from "components/Share";
import React from "react";
import { FC, useEffect } from "react";
import { Helmet } from "react-helmet";
import ProductCard from "../components/ProductCard/ProductCard";
import { useActionCreators } from "../hooks/useAC";
import { UseTypedSelector } from "../hooks/useTypedSelector";
import loseImg from "assets/icons/lose-icon.png";
const ProductContent: FC = () => {
  const { getProductsThunk } = useActionCreators();
  const { products } = UseTypedSelector((state) => state.ProductContent);

  useEffect(() => {
    getProductsThunk();
  }, []);
  const path = window.location.pathname + window.location.search;

  return (
    <>
      <Helmet defer={false} onChangeClientState={(state) => console.log(state)}>
        {/* <!-- Primary Meta Tags --> */}
        <title>Hello</title>
        <meta name="title" content="Hello" />
        <meta name="description" content="Its my website" />

        {/* <!-- Open Graph / Facebook --/> */}
        <meta property="og:type" content="website" />
        <meta
          property="og:url"
          content="https://liu1331.github.io/productContent"
        />
        <meta property="og:title" content="Hello" />
        <meta property="og:description" content="Its my website" />
        <meta property="og:image" content={loseImg} />

        {/* <!-- Twitter --/> */}
        <meta property="twitter:card" content="summary_large_image" />
        <meta
          property="twitter:url"
          content="https://liu1331.github.io/productContent"
        />
        <meta property="twitter:title" content="Hello" />
        <meta property="twitter:description" content="Its my website" />
        <meta property="twitter:image" content={loseImg} />
      </Helmet>
      <div className="container">
        <div className="ProductContent_items" style={{ paddingTop: "20px" }}>
          {products.map((product) => (
            <ProductCard key={product.productName} {...product} />
          ))}
          <Share
            shareLink="https://liu1331.github.io/productContent"
            shareTitle="Content"
          />
        </div>
      </div>
    </>
  );
};

export default ProductContent;
